@charset "UTF-8";
* {
  box-sizing: border-box !important;
}

.text-center {
  text-align: center !important;
}

.text-bold {
  font-weight: bold !important;
}

.dropdown-header {
  background-color: #eee;
  color: #73818f;
  white-space: nowrap;
  padding: 8px 20px;
  font-size: .765625rem;
}

.dropdown-item.dropdown-item {
  padding: 10px 20px;
  position: relative;
  transition: background-color .3s ease-in-out;
}

.dropdown-item.dropdown-item i {
  display: inline-block;
  width: 20px;
  margin-right: 10px;
  margin-left: -10px;
  color: #c8ced3;
  text-align: center;
}

.dropdown-item.dropdown-item .badge {
  float: right;
}

.navbar-custom-menu > .navbar-nav > li > .dropdown-menu {
  right: 30px;
  top: 40px;
}

.skin-blue .main-header .navbar .nav .open.open > a {
  background-color: transparent;
}

.user-menu.user-menu.user-menu.user-menu.user-menu {
  padding-right: 20px;
}

.user-menu.user-menu.user-menu.user-menu.user-menu > a {
  outline: none;
  transition-property: transform;
  transition-duration: .3s;
  transition-timing-function: ease-in-out;
}

.user-menu.user-menu.user-menu.user-menu.user-menu > a:hover {
  background-color: transparent;
  transform: scale(1.2);
}

.navbar-nav > .user-menu .user-image {
  margin-right: 0px;
}

.navbar-nav > .user-menu > .dropdown-menu {
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.7);
  width: 280px;
  border-radius: 10px;
  border: none;
  overflow: hidden;
}

.navbar-nav > .user-menu > .dropdown-menu li {
  overflow: hidden;
}

.navbar-nav > .user-menu > .dropdown-menu li:last-child {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.navbar-nav > .user-menu > .dropdown-menu > li.user-header {
  height: 100%;
  width: 100%;
  background-color: #eeeeee;
  display: inline-block;
  padding: 4px 5px;
  margin-bottom: -5px;
}

.navbar-nav > .user-menu > .dropdown-menu > li.user-header > img {
  height: 40px;
  line-height: 40px;
  width: 40px;
  float: left;
}

.navbar-nav > .user-menu > .dropdown-menu > li.user-header p {
  float: left;
  width: calc(100% - 40px);
  color: black;
  height: 50%;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 5px;
  text-align: left;
}

.navbar-nav > .user-menu > .dropdown-menu > li.user-header p:first-child {
  color: red;
}

.navbar-nav > .user-menu > .dropdown-menu > .user-body {
  border-top: none;
}

.iconAnim {
  transition-property: background-color;
  transition-duration: .3s;
  transition-timing-function: ease-in-out;
}

.iconAnim i, .iconAnim span {
  transition-property: transform;
  transition-duration: .3s;
  transition-timing-function: linear;
}

.iconAnim:hover i, .iconAnim:hover span {
  transform: translateY(-5px);
}

.sidebar-menu.sidebar-menu > li:hover > a {
  border-left: 2px solid #00c0ef;
  transition-property: background-color, color, border;
  transition-duration: .3s;
  transition-timing-function: ease-in-out;
}

/* 子選單 */
.skin-blue .sidebar-menu .treeview-menu > li > a:hover {
  border-left: 2px solid #00c0ef;
  transition-property: color, border;
  transition-duration: .3s;
  transition-timing-function: ease-in-out;
}

.skin-blue .main-header .navbar,
.skin-blue .main-header .logo {
  transition-property: background-color;
  transition-timing-function: ease-in-out;
  transition-duration: .3s;
  background-color: #fff;
  border-bottom: 1px solid #c8ced3;
}

.skin-blue .main-header .navbar .nav > li > a,
.skin-blue .main-header .navbar .sidebar-toggle {
  color: #73818f;
}

.skin-blue .main-header .navbar .nav > li > a:hover {
  background-color: transparent;
  color: #120000;
}

.skin-blue .main-header .logo:hover {
  background-color: #fff;
}

.skin-blue .main-header .navbar .sidebar-toggle {
  transition-duration: .3s;
  transition-timing-function: ease-in-out;
  transition-property: transform;
}

.skin-blue .main-header .navbar .sidebar-toggle:hover {
  background-color: transparent;
  color: #120000;
  transform: scale(1.3);
}

.logoImg {
  width: 89px;
  height: 25px;
  display: inline-block;
  vertical-align: middle;
  border-style: none;
  margin: 0;
  padding: 0;
}

